body {
  text-align: center;
  background-color: #f2f2f2;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 600px;
  background-color: #fff;
  border-radius: 5px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.breed-select {
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

.loader {
  text-align: center;
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  font-size: 18px;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  text-align: center;
  color: red;
  margin: 20px 0;
  font-size: 18px;
  display: none;
}

.cat-info {
  text-align: center;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 20px;
  display: none;
}

.cat-info h2 {
  margin-bottom: 10px;
  font-size: 24px;
}

.cat-info p {
  margin: 10px 0;
  font-size: 18px;
}

.cat-info img {
  max-width: 100%;
  height: auto;
  margin-top: 20px;
}

